import Preloader from '../../common/Preloader/Preloader';
import '../Person.scss';
import userPhoto from '../../../assets/img/userDF.png'
// import ProfileStatus from '../ProfileStatus/ProfileStatus';
import ProfileStatusWithHooks from '../ProfileStatus/ProfileStatusWithHooks';

const PersonInfo = (props) => {
    if (!props.profile) {
        return <Preloader />
    }
    return (
        <div>
            <div className="person__header"></div>
            <div className="person__avatar"><img className="person__avatar--img" src={props.profile.photos.large != null ? props.profile.photos.large : userPhoto} alt="Аватар" srcset="" /></div>
            
            <div className="person__info">
                <h3 className="person__name">{props.profile.fullName}</h3>
                <div className="person__line"></div>
                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
                <ul>
                    <li> <span>Дата рождения:</span>  2 января</li>
                    <li> <span>Город:</span>  Минск</li>
                    <li> <span>Образование:</span>  БГУ'11</li>
                    <li> <span>Вебсайт:</span> ---</li>
                </ul>
            </div>
        </div>

    ) 
}

export default PersonInfo;